.main {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 280px));
  grid-gap: 16px;
  margin-bottom: 92px;
  justify-content: center;
  justify-items: center;
  margin-top: 50px;
}
.container {
  object-fit: cover;
  width: 240px;
  border: 1px solid rgba(34, 34, 34, 0.287);
  height: 300px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: column;
  border-radius: 15px;
  transition: 0.7s;
}
.container:hover {
  box-shadow: 10px 20px 30px rgba(0, 0, 0, 0.5);
  transition: 0.7s;
}

.name {
  font-family: "Oxygen", sans-serif;
  font-weight: 700;
  margin-left: 25px;
}
.img {
  width: 240px;
  height: 180px;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
}

.cost {
  margin-left: 25px;
  font-family: "Oxygen", sans-serif;
  font-weight: 300;
}

.button {
  margin-left: 90px;
  transition: 0.7s;
  font-family: "Oxygen", sans-serif;
  font-weight: 700;
  font-size: 16px;
  width: 60px;
  border: none;
  box-shadow: 1px 1px 1px #7e7979e5;
  color: black;
  background-color: white;
}
.button:hover {
  cursor: pointer;
  background-color: black;
  color: white;
  transition: 0.7s;
}
